
@font-face {
	font-family: "agency fb";
	src: url(../static/AgencyFB.ttf);
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100%;
  height: 100%;
}
#nav{
	width: 300px;
	height: 100%;
	background-color: #28274E;
	color: #FBCF9D;
}
#nav li{
	font-size: 45px;
	font-family: "agency fb";
	padding-left: 60px;
	cursor: pointer;
}
#nav a{
	color: #FBCF9D;
}
#nav img{
	width: 300px;
	height: 270px;
}
#main{
	width: calc(100% - 300px);
	height: 100%;
	background-color: #31325F;
}



